<template>
  <div class="admin-page-home">
    <div class="home-container">
      <div class="information-card-group-container">
        <!--  -->
        <div class="card">
          <div class="card-main-container">
            <div class="card-icon">
              <i class="bi bi-inbox-fill"></i>
            </div>
            <div class="card-content">
              <div class="number">{{ productsLength }}</div>
              <div class="title"></div>
            </div>
          </div>
          <router-link
            class="view-more-container"
            :to="{ name: 'AdminProducts' }"
            @click="$emit('change-current-page', 'AdminProducts')"
            ><div class="title"></div>
            <i class="bi bi-arrow-right-circle"></i
          ></router-link>
        </div>
        <!--  -->
        <div class="card">
          <div class="card-main-container">
            <div class="card-icon">
              <i class="bi bi-cart3"></i>
            </div>
            <div class="card-content">
              <div class="number">{{ orderList?.length }}</div>
              <div class="title"></div>
            </div>
          </div>
          <router-link
            class="view-more-container"
            :to="{ name: 'AdminOrders' }"
            @click="$emit('change-current-page', 'AdminOrders')"
            ><div class="title"></div>
            <i class="bi bi-arrow-right-circle"></i
          ></router-link>
        </div>
        <!--  -->
        <div class="card">
          <div class="card-main-container">
            <div class="card-icon">
              <i class="bi bi-receipt"></i>
            </div>
            <div class="card-content">
              <div class="number">{{ couponsLength }}</div>
              <div class="title"></div>
            </div>
          </div>
          <router-link
            class="view-more-container"
            :to="{ name: 'AdminCoupons' }"
            @click="$emit('change-current-page', 'AdminCoupons')"
            ><div class="title"></div>
            <i class="bi bi-arrow-right-circle"></i
          ></router-link>
        </div>
        <!--  -->
        <div class="card">
          <div class="card-main-container">
            <div class="card-icon">
              <i class="bi bi-bank"></i>
            </div>
            <div class="card-content">
              <div class="number">${{ $filters.currency(getTotalIncome) }}</div>
              <div class="title"></div>
            </div>
          </div>
          <router-link
            class="view-more-container"
            :to="{ name: 'AdminOrders' }"
            @click="$emit('change-current-page', 'AdminOrders')"
            ><div class="title"></div>
            <i class="bi bi-arrow-right-circle"></i
          ></router-link>
        </div>
      </div>
      <div class="chart-group-container">
        <ProductsRankingChart class="order-chart" />
        <OrdersChart class="product-chart" />
      </div>
    </div>
  </div>
</template>

<script>
import ProductsRankingChart from "../../components/adminPages/ProductsRankingChart.vue";
import OrdersChart from "../../components/adminPages/OrdersChart.vue";

export default {
  components: {
    ProductsRankingChart,
    OrdersChart,
  },
  //  emitter
  inject: ["emitter"],
  data() {
    return {
      productsLength: 0, // 
      orderList: [], // API
      couponsLength: 0, // 
    };
  },
  computed: {
    //  
    getTotalIncome() {
      if (this.orderList.length > 0) {
        return this.orderList
          .filter((order) => {
            return order.is_paid;
          })
          .map((order) => {
            return order.total;
          })
          .reduce((a, b) => {
            return a + b;
          });
      } else {
        return 0;
      }
    },
  },
  methods: {
    // 
    getProducts() {
      this.isLoading = true;
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/products/all`;

      this.$http.get(api).then((res) => {
        if (res.data.success) {
          this.productsLength = Object.keys(res.data.products).length;
        }
      });
    },
    // 
    getOrders(page = 1) {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/orders?page=${page}`;

      this.$http.get(api).then((res) => {
        if (res.data.success) {
          this.orderList = res.data.orders;
        }
      });
    },
    // 
    getCoupons(page = 1) {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/coupons?page=${page}`;

      this.$http.get(api).then((res) => {
        if (res.data.success) {
          this.couponsLength = res.data.coupons.length;
          this.isLoading = false;
        }
      });
    },
    // 
    goUserHomePage() {
      // 
      this.$swal
        .fire({
          title: "",
          text: "",
          icon: "info",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          cancelButtonText: "",
          confirmButtonText: "",
        })
        .then((result) => {
          if (result.isConfirmed) {
            this.$swal.fire("!", "", "success");
            this.$router.push("/");
          }
        });
    },
  },
  created() {
    // Navbar
    this.$emit("change-navbar-page-title", "");
    // Navbar
    this.$emit("change-navbar-btn-title", "");
    // 
    this.getProducts();
    // 
    this.getOrders();
    // 
    this.getCoupons();
    // Navbar 
    this.emitter.on("open-modal", (status) => {
      if (status === "") {
        this.goUserHomePage();
      }
    });
  },
};
</script>
